import React, { FC, useCallback, useState } from "react";

import { Icon, IconName } from "../../Icon/Icon";
import Typography from "../../Typography/Typography";

import {
	LeftBar,
	StyledGlobalNavigationItemProps,
	StyledGlobalNavLink,
	TooltipAccentBar,
	TooltipAccentTriangle,
	TooltipItem,
	TooltipWrapper,
} from "./GlobalNavigationItem.styles";

export interface GlobalNavigationItemProps extends StyledGlobalNavigationItemProps {
	dataQA: string
	ariaLabel?: string
	menuIcon: IconName
	tooltipHeader?: string
	className?: string
}

const GlobalNavigationItem: FC<GlobalNavigationItemProps> = ({
	dataQA,
	ariaLabel,
	menuIcon,
	isActive,
	tooltipHeader,
	accentColor,
	accentBackgroundImage,
	hideActiveStateBar = false,
	className,
	m,
	mt,
	mb,
	mr,
	ml,
}) => {
	const [isVisible, setIsVisible] = useState(false);

	const handleMouseOver = useCallback(() => setIsVisible(true), []);
	const handleMouseOut = useCallback(() => setIsVisible(false), []);

	const renderedTooltip = isVisible && (
		<TooltipItem
			aria-expanded
			data-qa={`${dataQA}-tooltip-wrapper`}
		>
			<TooltipAccentTriangle data-qa={`${dataQA}-tooltip-accent-triangle`} accentColor={accentColor} />
			<TooltipAccentBar
				data-qa={`${dataQA}-tooltip-accent-bar`}
				accentColor={accentColor}
				accentBackgroundImage={accentBackgroundImage}
			/>
			<Typography
				dataQA={`${dataQA}-tooltip-header`}
				variant="displaySmall"
				lineHeight="normal"
				align="center"
			>
				{tooltipHeader}
			</Typography>
		</TooltipItem>
	);

	return (
		<TooltipWrapper
			onMouseEnter={handleMouseOver}
			onMouseLeave={handleMouseOut}
			accentColor={accentColor}
		>
			{!hideActiveStateBar && (
				<LeftBar
					data-qa={`${dataQA}-left-bar`}
					isActive={isVisible || isActive}
					accentColor={accentColor}
				/>
			)}

			<StyledGlobalNavLink
				data-qa={dataQA}
				aria-label={ariaLabel}
				isActive={isActive}
				accentColor={accentColor}
				accentBackgroundImage={accentBackgroundImage}
				className={className}
				m={m}
				mt={mt}
				mb={mb}
				mr={mr}
				ml={ml}
			>
				<Icon data-qa={`${dataQA}-menu-icon`} name={menuIcon} size={20} />
			</StyledGlobalNavLink>
			{renderedTooltip}
		</TooltipWrapper>
	);
};

GlobalNavigationItem.displayName = "GlobalNavigationItem";
export default GlobalNavigationItem;
